<template>
	<div id="norms">
		<div class="mask" v-if='showMask'>
			<div class="norms-cover">
				<div class="norms-header">
					<div class="norms-left">
						<div class="norms-imgWrap">
							<img class="norms-img" style="width: 100%;height: 100%;" :src="goodsInfo.images[0].imagePath" alt=""/>
						</div>
					</div>
					<div class="norms-info">
						<div class="price"><em>￥</em>{{goodsImmediately.discountPrice}}<em v-if="packings.length > 0">+</em>{{goods.packObj.price}} </div>
						<div v-if="Object.keys(goods).length >0	">已选：{{goods.spec.value}}  {{sizeObjArr[0].size}}</div>
					</div>
					<div class="norms-cancel" @click="$emit('hideMask','cancel')">
						<img src="../../assets/home_btn_del@2x.png"/>
					</div>
				</div>
				<div class="norms-content">
					<div class="type clearfix">
						<div>规格</div>
						<template v-for="(item,index) in specArr">
							<span @click="chooseSpec(item,index)" :class="{active:item.choosed}">{{item.value}}</span>
						</template>
					</div>
					<div class="type clearfix">
						<div>尺寸</div>
						<template v-for="(item,index) in newsizeArr">
							<span @click="chooseSize(item,index)" :class="{active:item.choosed}">{{item.size}}</span>
						</template>
					</div>
					<div class="type clearfix" v-if="packings.length > 0">
						<div>包装</div>
						<template v-for="(item,index) in packArr">
							<span @click="choosePack(item,index)" :class="{active:item.choosed}" v-if="packArr.length > 0">{{item.value.name}}</span>
						</template>
					</div>
				</div>
				<div class="norms-num clearfix" v-if="showAmount">
					<div class="amount fl-left">
						<span>数量</span><span>(库存：{{goodsImmediately.store}}件)</span>
					</div>
					<div class="numbox fl-right" v-if="goodsImmediately.isUp == 1 && goodsImmediately.isSoldOut != 1">
						<span class="numbox-minus control" @click="number<2?'':changeNum('minus')"><img src="../../assets/minus-icon.png"/></span>
						<input class="numbox-input" type="number" :value="number" disabled >
						<span class="numbox-add control" @click="number>=goodsImmediately.store?'':changeNum('add')"><img src="../../assets/add-icon.png"/></span>
					</div>
					<div class="fl-right" style="color: #fe4436;" v-if="goodsImmediately.isUp === 0">
						已下架
					</div>
					<div class="fl-right" style="color: #fe4436;" v-if="goodsImmediately.isSoldOut === 1">
						已售罄
					</div>
					
				</div>
				<div class="norms-btn" @click="$emit('hideMask',goods,goodsImmediately)" v-if="showBtn">确定</div>
			</div>		
		</div>
	</div>
</template>

<script>
	
export default {
	props:{
		showMask:{
			type:Boolean,
			default:false
		},
		//显示数量
		showAmount:{
			type:Boolean,
			default:true
		},
		//显示底部按钮
		showBtn:{
			type:Boolean,
			default:true
		},
		specList:{
			type:Array,
			default:[]
		},
		sizeList:{
			type:Array,
			default:[]
		},
		packings:{
			type:Array,
			default:[]
		},
		skuList:{
			type:Array,
			default:[]
		},
//		goodsImmediately:{
//			type:Object,
//			default:{}
//		},
		goodsInfo:{
			type:Object,
			default:{}
		},
	},
	data(){
		return {
			number:1,//购买数量
			goods:{},
			newsizeArr:[],
			sizeObjArr:[],
			packObjArr:[],
			goodsImmediately:{},
		}
	},
	mounted(){
//		this.attrInit(this.specList,this.specArr)
//		this.attrInit(this.sizeList,this.sizeArr)
		this.goodsInit()
		
		this.chooseSpec(this.specArr[0])
		
	},
  components: {
  	
  },
  watch:{
  	
  },
  computed:{
  	//规格尺寸
  	specArr(){
  		console.log('specArr');
  		let newArr = [];
  		this.specList.forEach((item,index)=>{
				let newobj = {}
				if(index==0){
					newobj.choosed = true
				}else{
					newobj.choosed = false
				}
				newobj.value = item
				newArr.push(newobj)
  		})
  		return newArr;
  	},
		packArr(){
			let newArr = [];
			if(this.packings.length > 0){
				this.packings.forEach((item,index)=>{
						let newobj = {}
						if(index==0){
							newobj.choosed = true
						}else{
							newobj.choosed = false
						}
						newobj.value = item
						newArr.push(newobj)
				})
				return newArr;
			}else{
				return newArr;
			}
		},
  },
  
  methods:{
  	changeNum(args){
  		if(args == 'add'){
  			this.number = this.number + 1
  		}
  		if(args == 'minus'){
  			this.number = this.number - 1
  		}
			this.$set(this.goods,'num',this.number)
  	},
  	//选包装
  	choosePack(item,index){
  		console.log(this.packArr)
  		console.log(item)
  		this.packArr.forEach((item,index)=>{
  			item.choosed = false
  		})
  		item.choosed = true
			this.$set(this.goods,'packObj',item.value)
			this.$set(this.goods,'packingId',item.value.id)
			this.getGoodsImmediatelyDetails(this.sizeObjArr[0].skuId)
  	},
  	//选尺寸
  	chooseSize(item,index){
			this.newsizeArr.forEach((item, index)=>{
				item.choosed = false;
				this.$set(this.newsizeArr, index, item);
			})
			item.choosed = true;
			this.$set(this.newsizeArr, index, item);
			this.getGoodsImmediatelyDetails(item.skuId)
			
			for (var i = 0; i < this.newsizeArr.length; i++) {
				if(this.newsizeArr[i].choosed){
					this.sizeObjArr = []
					this.sizeObjArr.push(this.newsizeArr[i])
				}
			}
			this.$set(this.goods,'size',this.sizeObjArr[0])
			this.$set(this.goods,'skuId',this.sizeObjArr[0].skuId)
  	},
  	
  	//选规格
  	chooseSpec(item,index){
  		this.specArr.forEach((item)=>{
  			item.choosed  = false
  		})
			item.choosed = true
			
			this.$set(this.goods,'spec',item)
			
			let skuList = this.skuList
			
			let sizeArr = []
			
			skuList.forEach((opt)=>{
				if(opt.spec == item.value){
					sizeArr.push(opt)
					sizeArr.forEach((val,index)=>{
						if(index==0){
							val.choosed = true
						}else{
							val.choosed = false
						}
					})
					this.newsizeArr = sizeArr
//					this.sizeObjArr = []
				}
			})
			this.getGoodsImmediatelyDetails(sizeArr[0].skuId)
			this.sizeObjArr = []
			this.sizeObjArr.push(sizeArr[0])
			
			this.$set(this.goods,'size',this.sizeObjArr[0])
			this.$set(this.goods,'skuId',this.sizeObjArr[0].skuId)
  	},
  	getGoodsImmediatelyDetails(skuId){
			this.$Api.getGoodsImmediatelyDetails(skuId).then((res)=>{
				if(res.q.s==0){
					this.goodsImmediately = res.q.goodsImmediately
				}
			})
		},
  	//初始化对象
  	goodsInit(){
  		let skuId = this.sizeObjArr.skuId
  		let spuId = this.goodsInfo.spuId
  		let promId  = this.goodsInfo.promId
  		let packingId = ''
  		let packObj = ''
  		if(this.packings.length > 0){
  			packingId  = this.packArr[0].value.id
  			packObj = this.packArr[0].value
  		}else{
  			packingId = 0
  			packObj = {}
  		}
	  	this.goods = Object.assign({},this.goods,{
				'spec': this.specArr[0],
				'size': this.sizeObjArr[0],
				'num' : this.number,
				packingId,
				packObj,
				skuId,
				spuId,
				promId,
			})
  	},
  }
}
</script>

<style lang="scss">
	@import "../../sass/baseSass.scss";
	#norms{
		.mask{
			@include mask;
		}
		.norms-cover{
			position: fixed;
			bottom: 0;
			/*height: 9.33rem;*/
			width: 100%;
			background: #fff;
			.norms-header{
				position: relative;
				display: flex;
				width: 100%;
				height: 2.35rem;
				&:after{
					@include bottom;
				};
				.norms-left{
					width: 3.33rem;
				}
				.norms-imgWrap{
					position: relative;
					left: 0.4rem;
					bottom: 0.99rem;
					width: 2.93rem;
					height: 2.93rem;
					border-radius: 0.08rem;
					background: #fff;
					border: 1px solid $border-color;
					padding: 0.09rem;
				}
				.norms-info{
					flex: 1;
					margin-left: 0.4rem;
					display: flex;
					flex-direction: column;
					justify-content: center;
					line-height: 1;
					.price{
						padding-bottom: 0.37rem;
						color: #fe4436;
						font-size: 0.48rem;
						>em{
							font-size:0.35rem;
						}
					}
				}
				.norms-cancel{
					position: absolute;
					right: 0.4rem;
					top: 0.33rem;
					>img{
						width: 0.53rem;
						height: 0.53rem;
					}
				}
			}
			.norms-content{
				position: relative;
				max-height: 3.33*2rem;
				min-height: 2.27*2rem;
				overflow-y: auto;
				/*&:after{
					@include bottom;
				};*/
				.type{
					padding: 0.4rem 0 0 0.4rem;
					&:nth-of-type(2){
						padding-top: 0.16rem;
					};
					>div{
						font-size: 0.37rem;
						margin-bottom: 0.4rem;
						color: #333 !important;
					}
					>span{
						float: left;
						min-width: 2.0rem;
						display: inline-block;
						height: 0.8rem;
						line-height: 0.8rem;
						background: #e7e7e7;
						border-radius: 0.4rem;
						text-align: center;
				    /*padding: 0 0.48rem;*/
				    overflow: hidden;
				    text-overflow: ellipsis;
				    -webkit-line-clamp: 1;
				    -webkit-box-orient: vertical;
				    margin-right: 0.4rem;
				    margin-bottom: 0.27rem;
					}
					.active{
						color: #fff;
						background: #de7fef;
					}
				}
			}
			.norms-num{
				position: relative;
				padding: 0.53rem 0.4rem;
				border-top: 1px solid #ccc;
				/*height: 1.17rem;*/
				/*line-height: 1.17rem;*/
				/*&:after{
					@include bottom;
				};*/
				.amount{
					font-size: 0;
					>span{
						&:nth-of-type(1){
							font-size: 0.37rem;
							color: #333;
						};
						&:nth-of-type(2){
							font-size: 0.35rem;
							color: #999;
							padding-left: 0.33rem;
						};
					}
				}
				.numbox{
					/*width: 2.13rem;*/
					height: 0.4rem;	
					line-height: 0.4rem;
					font-size: 0;
					.control{
						display: inline-block;
						width:0.32rem;
						height: 0.32rem;
						line-height: 0.32rem;
						vertical-align: top;
					}
					.numbox-input{
					 	width: 1.6rem;
						height: 0.4rem;	
						line-height: 0.4rem;
						font-size: 0.4rem;
						color: #333;
						padding: 0 0.07rem;
						text-align: center;
					}
				}
			}
			.norms-btn{
				font-size: 0.48rem;
				width: 100%;
				height: 1.31rem;
				line-height: 1.31rem;
				text-align: center;
				color: #fff;
				@include bg-color;
			}
		}
	}
</style>


