<template>
	<div id="good-detail">
		<div>
			<div class="header">
				<img class="arrow-icon fl-left" src="../../assets/common_btn_arrow2@2x.png" @click="goBack"/>
				<img v-if="!$call.testType().wechat" class="share-icon fl-right" src="../../assets/common_btn_share@2x.png" @click="invokeShare"/>
				<img class="cart-icon fl-right" src="../../assets/common_btn_shopping_cart@2x.png" @click="toCart"/>
			</div>
			<van-swipe :autoplay="3000">
				<van-swipe-item v-for="(image, index) in goods.images" :key="index">
					<img :src="image.imagePath" class="swipe-img" alt="" style="height: 10rem;"/>
				</van-swipe-item>
			</van-swipe>
			
			<div class="detail-info mb20">
				<div class="goods-name" v-if="goods.orgSummery">
					<span class="name-label" v-if="goods.orgSummery.orgId==0">自营</span>
					<span class="name-label" v-if="goods.orgSummery.orgId!=0">加盟</span>
					{{goods.name}}
				</div>
				<div class="prices">
					<span class="price-label">抢购价</span>
					<span class="price">￥ <em>{{goodsImmediately.discountPrice}}</em></span>
					<del class="oprice">￥{{goodsImmediately.originalPrice}}</del> 
				</div>
				
				<div class="tag-list clearfix" v-for="(item,index) in goods.tags" :key="index">
					<div class="tag-item">{{item.name}}</div>
				</div>
			</div>
			
			<template v-if="goods.type==5">
				<div class="explain mb20" @click="toExpain">
					定制说明
					<img src="../../assets/arrow_gray.png" class="arrow_gray">
				</div>
			</template>
			
			<template v-if="goods.type==1">
				<div class="explain mb20" @click="chooseNorms">
					选择规格尺寸
					<img src="../../assets/arrow_gray.png" class="arrow_gray">
				</div>
				<norms 
					v-on:hideMask="hideMask" 
					:showMask="showMask" 
					:specList="specList"
					:sizeList="sizeList"
					:skuList="skuList"
					:packings="packings"
					:goodsInfo="goods"
					v-if= "(specList.length > 0 || sizeList.length > 0) && goodsImmediately"></norms>
			</template>
			
			<div class="comment mb20" v-if="goods.praiseSummery">
				<div class="comment-bar">
					用户评价({{goods.praiseSummery.praiseCount}})
					<!--<img src="../../assets/arrow_gray.png" class="arrow_gray">-->
					<div class="start_sel">
						<i class="star" v-bind:class="'star-'+ parseInt(goods.praiseSummery.praiseValue)"></i><span>{{goods.praiseSummery.praiseValue}}分</span>
					</div>
				</div>
				<div class="comment-main">
					<div class="list-item clearfix" v-if="goods.comment.content !=''">
						<div class="item-img fl-left">
							<img :src="goods.comment.facePath" alt="">
						</div>
						<div class="item-info fl-left">
							<h4 class="text-over">{{goods.comment.name}}</h4>
							<p>{{goods.comment.comTime}}</p>
							<div class="start_sel">
								<div class="star" v-bind:class="'star-'+ parseInt(goods.comment.starNum)"></div>
							</div>
						</div>
					</div>
					<p class="comment-desc" v-if="goods.comment.content !=''">{{goods.comment.content}}</p>
					<p class="good-title text-over" v-if="goods.comment.content !=''">{{goods.name}}</p>
					<div class="comment-more" @click="toCommentList">查看全部评价</div>
				</div>
			</div>
			<div class="store mb20" v-if="goods.orgSummery">
				<router-link :to="{ name : 'ShopGoodsList' , query : { orgId: goods.orgSummery.orgId }}">
					<div class="list-item clearfix">
						<div class="item-img fl-left">
							<img :src="goods.orgSummery.iconPath" alt="">
						</div>
						<div class="item-info fl-left">
							<h4>{{goods.orgSummery.orgName}}</h4>
							<div>{{goods.orgSummery.description}}</div>
						</div>
						<img src="../../assets/arrow_gray.png" class="arrow_gray">
					</div>
				</router-link>
			</div>
			<div class="good-article">
				<h4>商品详情</h4>
				<div class="article-main" v-html="article.content"></div>
			</div>
		</div>
		
		<!--定制的底部-->
		<template v-if="goods.type==5">
			<div class="btn">
				<div class="service fl-left">
					<img src="../../assets/ico_service.png">
					<span>客服</span>
				</div>
				<div class="customized-btn fl-left" @click="toCustomizedEdit(goods.skuId,goods.spuId,skuList,goodsImmediately.discountPrice,goods.name,goods,goodsImmediately)">我要定制</div>
			</div>
		</template>
		
		<!--普通商品底部-->
		<template v-if="goods.type==1">
			<div class="btn2">
				<div class="service fl-left">
					<img src="../../assets/ico_service.png">
					<span>客服</span>
				</div>
				<div class="add-btn btns fl-left" @click="addCart">加入购物车</div>
				<div class="send-btn btns fl-left" @click="sendToFriend">送给好友</div>
			</div>
		</template>
	</div>
</template>

<script>
import Norms from "@/components/Norms/Norms";

export default {
  data() {
    return {
      type: 2,
      enterType: "",
      showMask: false,
      article: "",
      specList: [],
      sizeList: [],
      skuList: [],
      packings:[],
      goods: "",
      goodsImmediately: ""
    };
  },
  components: {
    Norms
  },
  watch: {},
  created() {
    this.$Api.untilSessionId().then(() => {
      this.getGoodsDetail();
      this.getGoodsImmediatelyDetails();
    });
  },
  methods: {
//	goBack(){
//    if(this.$route.query.mobile == 1){
//      backward({last:true})
//    }else{
//    	this.$router.back(-1)
//    }
//		},
		initShare(){
//			console.log("https://"+location.hostname+"/gift-fly/dist/#/goodDetail?fromShare=1&"+this.shareInfo.shareUrl.split('?')[1])
			let params = {
				title :this.shareInfo.shareTitle,
				desc  :this.shareInfo.shareText,
				link  :"https://"+location.hostname+"/gift-fly/dist/#/goodDetail?fromShare=1&"+this.shareInfo.shareUrl.split('?')[1],
				imgurl:this.shareInfo.iconPath
			}
//				console.log(this.$wechat.iniShare)
			this.$wechat.iniShare(params)
		},
    toCart() {
      this.$router.push({ name: "Cart" });
    },
    toCustomizedEdit(skuId,spuId,skuList,price,name,goods,goodsImmediately){
      localStorage.setItem('item',JSON.stringify({name:name,price,price}))
      localStorage.setItem('skuList', JSON.stringify(skuList))
      localStorage.setItem('goods', JSON.stringify(goods))
      localStorage.setItem('goodsImmediately', JSON.stringify(goodsImmediately))

      this.$router.push({name:'CustomizedEdit',query:{skuId:skuId,spuId:spuId}})
    },
    toExpain(){
      this.$router.push({name:'CustomizedExplain',query:{}})
    },
    toCommentList() {
      //				let skuid = this.goods.skuId
      let spuid = this.goods.spuId;
      this.$router.push({ name: "CommentList", params: { id: spuid } });
    },
    chooseNorms() {
      this.showMask = true;
      this.enterType = 1;
    },
    addCart() {
      this.showMask = true;
      this.enterType = 2;
    },
    sendToFriend() {
      this.showMask = true;
      this.enterType = 3;
    },
    hideMask(goods, goodsImmediately) {
      console.log(goods);
      console.log(goodsImmediately);
      this.showMask = false;
      let enterType = this.enterType;
      if (goods != "cancel") {
        switch (enterType) {
          case 1:
            console.log(1);
            break;
          case 2:
            console.log(2);
            if (goodsImmediately.isUp != 1) {
              this.$toast("商品已下架");
            } else if (goodsImmediately.isSoldOut == 1) {
              this.$toast("商品已售罄");
            } else {
              this.getCartUpdate(goods);
            }
            break;
          case 3:
            if (goodsImmediately.isUp != 1) {
              this.$toast("商品已下架");
            } else if (goodsImmediately.isSoldOut == 1) {
              this.$toast("商品已售罄");
            } else {
              let giftObj = {
                action: 3,
                type: 1,
                sendType: 1,
                addresId:0,
                shop: [
                  {
                    orgId: this.goods.orgSummery.orgId,
                    orgName: this.goods.orgSummery.orgName,
                    cartInfos: [
                      {
                        name: this.goods.name,
                        imagePath: this.goods.images[0].imagePath,
                        productSize: goods.size.spec + " " + goods.size.size,
                        skuId: goods.skuId,
                        spuId: goods.spuId,
                        buyNum: goods.num,
                        discountPrice: goodsImmediately.discountPrice,
                        promId: goods.promId,
                        customId: goods.customId ? goods.customId : "0",
                        storeId: goods.storeId ? goods.storeId : "0",
                        packingId: goods.packingId ? goods.packingId : "0",
                        packing:goods.packObj
                      }
                    ]
                  }
                ]
              };
              localStorage.setItem('giftObj',JSON.stringify(giftObj))
              console.log(giftObj);
              if(this.$call.testType().wechat){
              	this.$router.push({
              	  name: "ConfirmOrder",
              	  params: {giftObj},
              	  query:{action:3,type: 1}
              	});
              }else{
              	invokeOrderSubmit(giftObj)
              }
            }
            break;
          default:
            break;
        }
      }
    },
    getGoodsDetail() {
      let spuId = this.$route.query.spuId;
      let skuId = this.$route.query.skuId;
      let promId = this.$route.query.promId;

      let params = {
        spuId,
        skuId,
        promId
      };
      console.log(params);
				this.$Api.getGoodsDetails(params).then((res)=>{
        if (res.q.s == 0) {
          this.goods = res.q.goods;
          this.shareInfo = res.q.shareInfo;
          //						this.sizeList = res.q.sizeList
          //						this.specList = res.q.specList
          this.sizeList = res.q.sizeList.split(",");
          this.specList = Array.from(new Set(res.q.specList.split(",")));
					this.packings = res.q.goods.packings;
          this.skuList = res.q.skuList;
				
          this.getArticleDetails(res.q.goods.articleId);
					this.initShare()
        }
      });
    },
    getGoodsImmediatelyDetails() {
      let skuId = this.$route.query.skuId;
      this.$Api.getGoodsImmediatelyDetails(skuId).then(res => {
        if (res.q.s == 0) {
          this.goodsImmediately = res.q.goodsImmediately;
        }
      });
    },
    getCartUpdate(params) {
      let goods = {
        skuId: params.skuId,
        spuId: params.spuId,
        num: params.num,
        promId: params.promId,
        customId :params.customId,
        packingId: params.packingId
      };
      let goodsList = [];
      goodsList.push(goods);
      this.$Api.getCartUpdate(1, goodsList).then(res => {
        if (res.q.s == 0) {
          this.$toast("加入购物车成功");
        } else {
          this.$toast(res.q.d);
        }
      });
    },
    getArticleDetails(id) {
      this.$Api.getArticleDetails(id).then(res => {
        console.log(res);
        if (res.q.s == 0) {
          this.article = res.q.article;
        }
      });
    },
    invokeShare() {
      if (this.$call.testType().wechat) {
        this.$toast("点击分享");
        //					let params = this.shareInfo
        //					console.log(this.shareInfo)
      } else {
        let params = this.shareInfo;
        console.log(params);
        invokeShare(params);
      }
    }
  }
};
</script>

